import { Component, OnInit } from '@angular/core';
import { BlogService } from '../blog.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms'

@Component({
  selector: 'app-crud',
  templateUrl: './crud.component.html',
  styleUrls: ['./crud.component.css']
})
export class CrudComponent implements OnInit {

  blogForm: FormGroup;

  arrayOfObjects: any = [];

  fetchedData: any[] = []

  constructor(private blogService: BlogService, private _fb: FormBuilder) {
    this.createForm();
  }

  ngOnInit() {
    this.blogService.getBlogData().subscribe(result => {
      this.arrayOfObjects = result;
      this.fetchedData = [...this.arrayOfObjects]
    })
  }

  /**Reactive Form */
  public createForm(): void {
    this.blogForm = this._fb.group({
      title: [null, [Validators.required]],
      summary: [null, [Validators.required]],
    })
  }

  public submitForm() {
    if (this.blogForm.valid) {
      this.blogService.postBlogData(this.blogForm.value).subscribe(result => {
        this.fetchedData.push(result)
        this.blogForm.reset();
        console.log('Insertion Success')
      })
    } else {
      alert('Please, Fill the fields');
    }

  }

  public delete(_id: any) {
    this.blogService.deleteBlogData(_id).subscribe(result => {
      if (result == 'Deletion successful!') {
        console.log('delete success')
        const updatedBlogs = this.fetchedData.filter(d => d._id !== _id);
        this.fetchedData = updatedBlogs;
      } else {
        console.log('delete fail')
      }
    })
  }

}
