import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class BlogService {

  constructor(private http: HttpClient) { }

  
  getBlogData() {
    return this.http.get('http://model-env.j3a3qjzmrq.us-east-1.elasticbeanstalk.com/api/getBlog');
  }

  postBlogData(body) {
    return this.http.post('http://model-env.j3a3qjzmrq.us-east-1.elasticbeanstalk.com/api/postBlog', body);
  }

  deleteBlogData(_id: any) {
    return this.http.delete(`http://model-env.j3a3qjzmrq.us-east-1.elasticbeanstalk.com/api/deleteBlog/${_id}`);
  }

}
